import React from 'react';
import Logo from '../images/logo.png'
import Enquiry from '../images/Enquiry.png'
import { NavLink } from 'react-router-dom'
import './HeaderNav.css'


const HeaderNav = () => {

    const directToHome = () => {
        window.location.pathname='/';
    }

    return (
        <div className='HeaderBar'>
            <div className='logoBox'>
                <img src={Logo} alt="" className='logo' onClick={directToHome}/>
            </div>
            <div className='tabs'>
                <div className='tab1'>서비스</div>
                <div className='tab1'>물류시스템</div>
                <div className='tab1'>운영관리</div>
                <div className='tab1'>FAQ</div>
                <div className='tab1'>인재채용</div>
                <div className='tab1'>회사소개</div>
            </div>
            <NavLink to={'/enquiry'}>
                <img src={Enquiry} alt="" className='enquiry'/>
            </NavLink>
            
        </div>
    );
};

export default HeaderNav;